.card-header {
    font-family: 'Zen Dots', cursive; /* Shaolin-inspired font (use Google Fonts) */
    font-size: 1.5rem;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.card-body {
    font-family: 'Noto Sans', sans-serif;
    background-color: #f5f5f5; /* Light background to make the text pop */
}

.card {
    border-radius: 15px; /* Rounded corners for a smooth modern look */
    box-shadow: 0 4px 8px rgba(0,0,0,0.2); /* Soft shadow for depth */
    transition: transform 0.3s ease; /* Smooth hover effect */
}

.card:hover {
    transform: translateY(-5px); /* Hover effect to lift the card slightly */
}
